                        <div class="text text--white h1" style='font-weight:800; letter-spacing: 6px; line-height: 4rem;'>Who's <i>this</i> guy?</div>
                        <div class="text text--white h2">Hi, I'm Greg Schoberth.</div>
                        <p class='text text--white' style="margin-top: 60px;"> 
                            I am a recent graduate of Arizona State’s software engineering program, originally from the third coast state -- Michigan.
                            Currently I am living in Bristol, Rhode Island.
                        </p>
                        <p class='text text--white'>
                            I enjoy bringing technology and design together to create solutions for complex ideas and problems. For me, putting the user’s
