                            such as <span class='orange h3'>Vue</span>, <span class='orange h3'>React</span>, & <span class='orange h3'>SASS</span> 
                            to create UI/UX  to convey complex information in easy to understand ways.
                        </p>
                        <p class='text text--white'>
                            When not knee deep in code, I enjoy boardgames, winning (hopefully) trivia nights, or delving into my own creations. 
                        </p>
                        <div class='text text--white'>
