                                <div class='featured-client'>
                                    <div class='section-subtitle section-subtitle--blue--500'>// Client</div>
                                    <div class='client-text'>Over The Sun Productions</div>
                                </div>
                                <div class='featured-role'>
                                    <div class='section-subtitle section-subtitle--blue--500'>// Primary Role</div>
                                    <div class='role-text'>Front-end Developer</div>
                                    <div class='role-text'>UI/UX Designer</div>
                                </div>

                                <div class='role-text text--orange' style="text-decoration: underline;">CURRENTLY IN DEVELOPMENT</div>

                                <p class='text text--black'>
                                    SIMOC is an interactive model of an off-world community. It employs an agent-based model built
                                    based on published data and research from institutions such as NASA and numerous universities.
                                    SIMOC was developed utilizing a combination of a web based front-end and Python back-end.  
                                </p>
                                <p class='text text--black'>
                                    This web app was originally proposed to ASU as a senior software engineering capstone project. 
                                    As part of the capstone team, my primary responsibility was the design, direction and implementation of 
                                    the UI/UX. This included implementing and updating the UI with live data received from the back-end.  
                                    My secondary role involved working on the back-end middleware.
                                </p>
                                <p class='text text--black'>
                                    As a capstone project, the UI was originally created utilizing Bootstrap 4 for layout and theming purposes. 
                                    As we progressed, it became obvious that we would need to utilize a framework such as Vue.js for the front-end interface. 
                                    Vue allowed for features to be designed and implemented with more ease and speed. These included dashboard features 
                                    allowing the user to customize the data panels displayed and the presentation of live-feed data from the agent-model driven back-end.
                                </p>
                                <p class='text text--black'>
                                    Upon graduation and completion of the capstone project, I was offered the opportunity to remain on as the Front-end Developer, 
                                    continuing my existing responsibilities of the design and implementation of the entire UI for the web app.
                                </p>
                                <p class='text text--black'>
                                    During the next phase, SIMOC will be user tested by a volunteer team of teachers working with one of our launch partners, 
                                    the National Geographic Society. Upon completion of the next phase, additional partners include the Arizona Science Center and the Biosphere 2.
                                </p>
                                <div class='section-subtitle section-subtitle--blue--500'>// Additional Information At Website</div>
