                                <img class='featured-image' src="../assets/simoc-config.jpg">
                                <div class='section-subtitle section-subtitle--blue--500 force-flex-left'>// Session Configuration</div>
                                <img class='featured-image' src="../assets/simoc-dash.png">
                            </div>                            
