<script>
export default {
    data(){
        return{
            mobileMenu: false,
        
            emailData:{
                'name':"",
                'email':"",
                'message':""
            },

            isLoading: false,
            loaded:true,
        }
    },


    mounted(){
        this.loading();
    },

    methods:{
        submitEmail:async function(){
            try{
                const {data} = await this.$http.post("https://formspree.io/xkybynnx",{'message':this.emailData},{ headers: {'Content-Type': 'application/json'}})
                this.emailData ={
                    'name':"",
                    'email':"",
                    'message':""
                }
            } catch(err){
                console.log(err)
            }
        },

        loading:function(){
            setTimeout(() => {
                this.isLoading = false
                this.finishedLoading()
            },3000)
        },

        finishedLoading:function(){
            setTimeout(()=>{
                this.loaded = true
            },1650)
        },
    }
}
</script>



<style lang="scss" scoped>

