        width: 100%;
        z-index:2000;
        opacity: 0;
        transition: opacity .5s ease;
    }

    .spinner__isLoading{
        opacity: 1;
    }

    .load__spinner{
        position:relative;
        width: 50%;
        min-height: 64px;
        min-width: 64px;
        border: 1px solid rgba($blue-100,.1);
        border-radius: 50%;
        box-sizing: border-box;
        &:after{
            content:"";
            display:block;
            padding-bottom: 100%;
        }
    }

    .load__name{
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate(-50%,-50%);
        text-transform: uppercase;
        color: $white;
        font-family:"montserrat";
        font-weight: 800;
        letter-spacing: 5px;

        display:none;

        @include viewport-7{            
            font-size: 24px;
            display:block;
        }

        @include viewport-9{
            display:block;
            font-size: 32px;
        }

    }

    .load__spinner--thick{
        
        z-index:1003;
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate(-50%,-50%) rotate(45deg);
        min-width: 100%;
        min-height: 100%;
        animation: spinThick 15s ease-in-out infinite;
        box-sizing: border-box;
        &:before,&:after{
            width: 100%;
            height: 100%;
            content:"";
            position:absolute;
            border: 4px solid transparent;
            border-radius: 50%;
        }

        &:before{
            box-sizing: border-box;
            border-top: 4px solid $blue;
        }

        &:after{
            box-sizing: border-box;
            border-bottom: 4px solid $blue;
        }

    }

    .load__spinner--ellipsis{
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate(-50%,-50%);
        min-width: 8%;
        min-height: 8%;
        background-color: $white;
        border-radius: 50%;

        @include viewport-7{
            display:none;
        }

    }

    .load__spinner--mid{
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate(-50%,-50%) rotate(30deg);
        min-width: 85%;
        min-height: 85%;
        animation: spinMid 60s linear infinite;
        box-sizing: border-box;
