import Vue from 'vue';
import App from './App.vue';
import router from './router';
import axios from 'axios'



import { library } from '@fortawesome/fontawesome-svg-core'
import { faSun,faBars, faTimes, faArrowDown} from '@fortawesome/free-solid-svg-icons'
import {faLinkedin,faCodepen,faTwitter,faGithub} from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faSun,faBars,faTimes, faArrowDown,faLinkedin,faCodepen,faTwitter,faGithub)

Vue.component('fa-icon', FontAwesomeIcon)

Vue.config.productionTip = false;


Vue.prototype.$http = axios

new Vue({
  router,
  render: h => h(App),
}).$mount('#app');
